.product-labels-list {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}
.product-label {
  font-weight: 700;
  font-size: 10px;
  line-height: 1;
  padding: 6px 10px;
  border-radius: 4px;
  color: #fff;
  pointer-events: none;
  text-align: center;
  &:not(:last-child) {
    margin-bottom: 5px;
  }
  &_type-1 {
    background: var(--color-label-1);
  }
  &_type-2 {
    background: var(--color-label-2);
  }
  &_type-3 {
    background: var(--color-label-3);
  }
  &_type-4 {
    background: var(--color-label-4);
  }
}

.back-link {
  text-align: right;
  margin-top: 25px;
  a {
    font-size: 0.875rem;
    transition: color $trs1;
    display: inline-flex;
    align-items: center;
    color: var(--color-muted);
    .icon {
      width: 1em;
      height: 1em;
      margin-right: 0.5em;
    }
    &[data-touch], &[data-hover] {
      color: var(--color-accent-2);
    }
  }
}

.breadcrumbs {
  font-size: 0.875rem;
  line-height: 1.5;
  margin-bottom: 50px;
  margin-top: 75px;
  &__list {
    display: flex;
    flex-wrap: wrap;
    margin: 0 -10px -8px 0;
  }
  &__item {
    display: flex;
    align-items: center;
    margin: 0 10px 8px 0;
    .icon {
      width: 10px;
      height: 10px;
      margin-left: 10px;
    }
  }
  &__element {
    span {
      border-bottom: 1px dashed currentColor;
    }
    &:not(a) {
      color: var(--color-accent-2);
    }
  }
  @include media-breakpoint-down(lg) {
    margin: 50px 0 40px 0;
  }
}

.pdf-list {
  display: grid;
  grid-template-columns: repeat(2, minmax(300px, 440px));
  column-gap: var(--grid-gutter-x);
  row-gap: 40px;
  &__item {
    display: flex;
    .icon {
      width: 60px;
      height: 60px;
      flex-shrink: 0;
      margin-right: var(--grid-gutter-x);
    }
  }
  &__item-title {
    display: block;
    margin-bottom: 10px;
  }
  @include media-breakpoint-down(md) {
    grid-template-columns: auto;
    row-gap: 30px;
    &__item {
      .icon {
        width: 40px;
        height: 40px;
      }
    }
  }
}

.load-more {
  margin-top: 36px;
  text-align: center;
}

.pagination {
  margin-top: 36px;
  &__list {
    display: flex;
    justify-content: flex-end;
  }
  &__item {
    &:not(:last-child) {
      margin-right: 14px;
    }
  }
  &__link {
    display: flex;
    line-height: 16px;
    width: 16px;
    height: 16px;
    justify-content: center;
    align-items: center;
    text-align: center;
    transition: color $trs1;
    color: var(--color-muted);
    .icon {
      display: block;
      width: 100%;
      height: 100%;
    }
    &[data-touch], &[data-hover] {
      color: var(--color-accent-2);
    }
    &.is-active {
      color: var(--color-accent-2);
      font-weight: 700;
      pointer-events: none;
    }
  }
  @include media-breakpoint-down(lg) {
    &__list {
      justify-content: center;
    }
  }
}

.tab-head {
  border-bottom: 1px solid var(--color-borders);
  &__container {
    display: flex;
  }
  &__link {
    font-size: 18px;
    padding: 11px 36px;
    transition: background-color $trs1, color $trs1, border-color $trs1;
    border-radius: 4px 4px 0 0;
    text-align: center;
    &[data-touch], &[data-hover] {
      color: var(--color-accent-1);
    }
    &.is-active {
      background-color: var(--color-accent-1);
      color: #fff;
      pointer-events: none;
    }
  }
  @include media-breakpoint-down(lg) {
    padding-bottom: 12px;
    display: flex;
    &__container {
      flex-wrap: wrap;
      margin: 0 -12px -12px 0;
    }
    &__link {
      border-radius: 0;
      font-size: 16px;
      line-height: 20px;
      padding: 10px 20px;
      border: 1px solid var(--color-borders);
      margin: 0 12px 12px 0;
      &[data-touch], &[data-hover] {
        border-color: var(--color-accent-1);
      }
      &.is-active {
        border-color: var(--color-accent-1);
      }
    }
  }
  @include media-breakpoint-down(sm) {
    &__link {
      font-size: 14px;
    }
  }
}

.tag-list {
  display: flex;
  &__container {
    width: 100%;
    margin: 0 -12px -12px 0;
    display: flex;
    flex-wrap: wrap;
  }
  &__element {
    margin: 0 12px 12px 0;
  }
}

.tag-element {
  white-space: nowrap;
  border: 1px solid var(--color-borders);
  border-radius: 4px;
  color: var(--color-muted);
  font-size: 14px;
  line-height: 32px;
  padding: 0 18px;
  position: relative;
  transition: color .3s $out, border-color .3s $out;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  &::before {
    content: '';
    display: block;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    position: absolute;
    background-color: var(--color-accent-2);
    opacity: 0;
    transform: scaleX(0.7);
    transition: transform .3s $out, opacity .3s $out, background-color $trs1;
  }
  .icon {
    margin-right: 8px;
    width: 17px;
    height: 17px;
  }
  span, .icon {
    position: relative;
    z-index: 2;
  }
  &[data-touch], &[data-hover], &.is-active {
    color: #fff;
    border-color: var(--color-accent-2);
    &::before {
      transform: scaleX(1);
      opacity: 1;
    }
  }
  &.is-active {
    &[data-touch], &[data-hover] {
      border-color: var(--color-accent-2_2);
      &::before {
        background-color: var(--color-accent-2_2);
      }
    }
  }
}

.home-screen-banner {
  padding-bottom: 100px;
  --pv: 90px;
  &:first-child {
    padding-top: 0;
  }
  &__slide {
    &-container {
      min-height: 460px;
      padding-top: var(--pv);
      padding-bottom: calc(var(--pv) + 48px);
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: flex-start;
    }
    &-text-content {
      margin-bottom: 50px;
      width: 50%;
    }
    &-title {
      margin-bottom: 25px;
    }
    &-text {
      font-size: 1.625rem;
    }
  }
  .swiper-pagination {
    position: absolute;
    bottom: 36px;
    left: 50%;
    transform: translateX(-50%);
  }
  &__advantages-line {
    margin-top: 20px;
  }

  @include media-breakpoint-down(lg) {
    --pv: 60px;
    &__slide-container {
      min-height: 380px;
    }
    &__slide-text {
      font-size: 1.25rem;
    }
    &__advantages-line {
      margin-top: 40px;
    }
  }
  @include media-breakpoint-down(md) {
    &__slide-text {
      font-size: 1.125rem;
    }
    &__slide-text-content {
      width: initial;
    }
  }
  @include media-breakpoint-down(sm) {
    &__slide-container {
      padding-bottom: calc(var(--pv) + 35px);
      min-height: initial;
    }
    &__slide-text-content {
      margin-bottom: 40px;
    }
    .swiper-pagination {
      bottom: 25px;
    }
  }
} 

.advantages-line {
  &__container {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    column-gap: var(--grid-gutter-x);
  }
  &__element {
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    .icon {
      flex: 0 0 auto;
      height: 40px;
      margin-right: 12px;
    }
  }
  @include media-breakpoint-up(lg) {
    &__element {
      &:not(:last-child) {
        &::after {
          content: '';
          display: block;
          position: absolute;
          width: 1px;
          height: 100%;
          background-color: var(--color-borders);
          right: calc(var(--grid-gutter-x) / -2);
        }
      }
    }
  }
  @include media-breakpoint-down(lg) {
    &__container {
      grid-template-columns: repeat(2, 1fr);
      row-gap: 40px;
    }
    &__element {
      justify-content: flex-start;
    }
  }
  @include media-breakpoint-down(sm) {
    &__element {
      br {
        display: none;
      }
      .icon {
        height: 30px;
        margin-right: 10px;
      }
    }
  }
}

.socials {
  &__list {
    display: flex;
  }
  &__item {
    &:not(:last-child) {
      margin-right: 15px;
    }
  }
  &__link {
    display: block;
    font-size: 0;
    transition: color $trs1;
    color: var(--color-socials);
    .icon {
      display: block;
      height: 25px;
    }
    &[data-touch], &[data-hover] {
      color: var(--color-accent-2);
    }
  }
}

.count-calculator {
  display: inline-flex;
  &__button {
    width: 2em;
    height: 2em;
    border: 1px solid var(--color-borders);
    position: relative;
    transition: border-color $trs1;
    flex-shrink: 0;
    &::before, &::after {
      content: '';
      background-color: var(--color-accent-1);
      transition: background-color $trs1;
      position: absolute;
      left: 50%;
      top: 50%;
      transform: translate(-50%, -50%);
    }
    &::before {
      width: 1em;
      height: 1px; 
    }
    &_minus {
      border-radius: 4px 0 0 4px;
      
    }
    &_plus {
      border-radius: 0 4px 4px 0;
      &::after {
        height: 1em;
        width: 1px;
      }
    }
    &[data-touch], &[data-hover] {
      &::before, &::after {
        background-color: var(--color-accent-1_2);
      }
    }
  }
  &__input {
    border-style: solid;
    border-width: 1px 0 1px 0;
    border-color: var(--color-borders);
    font-size: 1em;
    line-height: calc(2em - 2px);
    padding: 0 0.5em;
    width: 3.25em;
    text-align: center;
    transition: border-color $trs1;
  }
  &[data-hover] {
    .count-calculator__button, .count-calculator__input {
      border-color: var(--color-accent-1);
    }
  }
  @include media-breakpoint-down(sm) {
    &__input {
      line-height: calc(2.5em - 2px);
    }
    &__button {
      width: 2.5em;
      height: 2.5em;
    }
  }
}

.buy-box {
  position: relative;
  &__button {
    transition: opacity $trs1, visibility $trs1;
  }
  &__calculator {
    opacity: 0;
    visibility: hidden;
    transition: opacity $trs1, visibility $trs1;
    position: absolute;
    width: 100%;
    top: 0;
    left: 0;
  }
  .count-calculator {
    width: 100%;
    &__button {
      width: 42px;
      height: 42px;
    }
    &__input {
      width: auto;
      line-height: 40px;
      flex: 1 1 auto;

    }
  }
  &_small {
    .count-calculator {
      &__button {
        height: 34px;
        width: 34px;
      }
      &__input {
        line-height: 32px;
      }
    }
  }
  &.is-active {
    .buy-box__button {
      opacity: 0;
      visibility: hidden;
    }
    .buy-box__calculator {
      opacity: 1;
      visibility: visible;
    }
  }
}

.packaging-list {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 0.5em;
  &__item {
    cursor: default;
    input {
      display: none;
      &:checked {
        & ~ span {
          border-color: var(--color-accent-1);
          pointer-events: none;
        }
      }
      &:disabled {
        & ~ span {
          background-color: var(--color-borders);
          color: #fff;
          pointer-events: none;
        }
      }
    }
    span {
      height: 2em;
      border: 1px solid var(--color-borders);
      border-radius: 4px;
      display: flex;
      align-items: center;
      justify-content: center;
      text-align: center;
      font-size: 1em;
      cursor: pointer;
      transition: border-color $trs1;
      &[data-touch], &[data-hover] {
        border-color: var(--color-accent-1);
      }
    }
  }
  @include media-breakpoint-down(sm) {
    &__item {
      span {
        height: 2.5em;
      }
    }
  }
}

.product-card {
  display: flex;
  flex-direction: column;
  min-height: 100%;
  position: relative;
  &::before {
    content: '';
    display: block;
    position: absolute;
    width: 100%;
    height: 100%;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background-color: #fff;
    border-radius: 4px;
    box-shadow: var(--block-shadow);
    transition: width $trs1, height $trs1;
  }
  &[data-hover] {
    &::before {
      width: calc(100% + 10px);
      height: calc(100% + 10px);
    }
    .product-labels-list {
      transform: translate(-5px, -5px);
    }
  }
  &__image {
    flex: 0 0 220px;
    position: relative;
    z-index: 2;
    .image {
      position: absolute;
      width: 100%;
      height: 100%;
      top: 0;
      left: 0;
      img {
        padding: 10px 5px 0 5px;
      }
    }
  }
  .product-labels-list {
    position: absolute;
    left: 6px;
    top: 6px;
    transition: transform $trs1;
  }
  &__content {
    flex: 1 1 auto;
    padding: 10px 10px 25px 10px;
    position: relative;
    z-index: 2;
    display: flex;
    flex-direction: column;
    align-items: stretch;
  }
  &__top {
    flex: 0 0 auto;
    &:not(:last-child) {
      margin-bottom: 20px;
    }
  }
  &__bottom {
    flex: 1 0 auto;
    display: flex;
    flex-direction: column;
    align-items: stretch;
  }
  &__label-info {
    font-size: 12px;
    padding-left: 1.6em;
    color: var(--color-muted);
    position: relative;
    margin-bottom: 15px;
    min-height: 1.25em;
    .icon {
      position: absolute;
      width: 1em;
      height: 1em;
      left: 0;
      top: 0.1em;
    }
  }
  &__title {
    display: block;
    font-size: 14px;
    -webkit-line-clamp: 3;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    overflow: hidden;
    min-height: 3.75em;
  }
  &__packaging {
    flex: 1 0 auto;
  }
  &__packaging-list {
    font-size: 12px;
  }
  &__packaging-title {
    display: block;
    margin-bottom: 10px;
    font-size: 12px;
    color: var(--color-muted);
  }
  &__last {
    margin-top: 20px;
    display: flex;
  }
  &__pricing {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    flex: 0 0 auto;
    margin-right: 15px;
    span {
      font-size: 0.9em;
    }
  }
  &__old-price {
    height: 14px;
    font-size: 12px;
    line-height: 14px;
    margin-bottom: 2px;
    color: var(--color-muted);
    position: relative;
  }
  &__old-price-element {
    &::after {
      content: '';
      display: block;
      position: absolute;
      width: 110%;
      height: 1px;
      background-color: var(--color-muted);
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%) rotate(-12deg);
    }
  }
  &__price {
    line-height: 1;
    font-weight: 700;
    font-size: 18px;
  }
  &__buy-box {
    flex: 1 1 auto;
    .button {
      min-width: initial;
      width: 100%;
    }
  }
  @include media-breakpoint-down(sm) {
    &__last {
      display: block;
    }
    &__pricing {
      flex-direction: row;
      align-items: flex-end;
      margin: 0 0 15px 0;
    }
    &__old-price {
      margin: 0 0 1px 10px;
      order: 2;
    }
    &__image {
      flex: 0 0 auto;
      .image {
        position: initial;
        width: 100%;
        height: auto;
        &::before {
          content: '';
          display: block;
          padding-top: 100%;
        }
      }
    }
    &__content {
      padding-top: 10px;
    }
  }
  @media screen and (max-width:359px) {
    &__title {
      font-size: 12px;
    }
    &__packaging-title, &__packaging-list {
      font-size: 10px;
    }
  }
}

.products-section {
  &__content {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    column-gap: 60px;
    row-gap: 55px;
  }
  @include media-breakpoint-down(lg) {
    &__content {
      grid-template-columns: repeat(2, 1fr);
      gap: var(--grid-gutter-x);
    }
  }
}

.product-category-block {
  position: relative;
  display: block;
  padding-bottom: 30px;
  &::before {
    content: '';
    display: block;
    position: absolute;
    width: 100%;
    height: 100%;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background-color: #fff;
    border-radius: 4px;
    box-shadow: var(--block-shadow);
    transition: width $trs1, height $trs1;
  }
  &[data-hover] {
    &::before {
      width: calc(100% + 10px);
      height: calc(100% + 10px);
    }
  }
  .image {
    border-radius: 4px;
    height: 170px;
    margin-bottom: 20px;
    img {
      padding: 40px 40px 0 40px;
    }
  }
  &__title {
    position: relative;
    z-index: 2;
    text-align: center;
    margin: 0;
    font-weight: 700;
    padding: 0 15px;
  }
  @include media-breakpoint-up(lg) {
    &__title {
      font-size: 1.25rem;
    }
  }
  @include media-breakpoint-down(sm) {
    padding-bottom: 20px;
    .image {
      height: initial;
      &::before {
        content: '';
        display: block;
        padding-top: 80%;
      }
      img {
        padding: 20px;
      }
    }
  }
}

.items-slider {
  .swiper-container {
    margin: calc(var(--grid-gutter-x) / -2);
  }
  .swiper-slide {
    padding: calc(var(--grid-gutter-x) / 2);
  }
  .swiper-button {
    bottom: calc(100% + calc(60px - var(--grid-gutter-x) / 2));
  }
  .swiper-button-prev {
    right: 52px;
  }
  .swiper-button-next {
    right: 0;
  }
  &__head {
    padding-right: 104px;
  }
  @include media-breakpoint-up(sm) {
    .swiper-pagination {
      display: none;
    }
  }
  @include media-breakpoint-down(lg) {
    .swiper-button {
      bottom: calc(100% + calc(53px - var(--grid-gutter-x) / 2));
    }
  }
  @include media-breakpoint-down(md) {
    .swiper-button {
      bottom: calc(100% + calc(47px - var(--grid-gutter-x) / 2));
    }
  }
  @include media-breakpoint-down(sm) {
    .swiper-container {
      overflow: visible;
    }
    .swiper-button {
      display: none;
    }
    .swiper-pagination {
      margin-top: 30px;
      justify-content: center;
      --color: var(--color-dark-pagination);
      --color-hover: var(--color-dark-pagination-hover);
      --color-active: var(--color-dark-pagination-active);
    }
    &__head {
      padding-right: 0;
    }
  }
}

.parallax-product-slider {
  position: relative;
  &__line {
    position: absolute;
    width: 2750px;
    height: auto;
    top: -30px;
    left: -460px;
  }
  @include media-breakpoint-up(lg) {
    &__background {
      background-color: transparent;
      position: absolute;
      width: 110%;
      height: calc(100% + var(--oversize));
      top: calc(var(--oversize) / -2);
      left: 0;
    }
    .swiper-container {
      overflow: visible;
    }
    .swiper-wrapper {
      align-items: flex-start;
    }
    .swiper-slide {
      width: 240px !important;
      box-sizing: content-box;
      &:not(:last-child) {
        padding-right: 60px;
      }
      &:nth-child(odd) {
        margin-top: 60px;
      }
    }
  }
  @include media-breakpoint-down(lg) {
    &__line, &__background {
      display: none;
    }
    .swiper-container {
      margin: calc(var(--grid-gutter-x) / -2);
    }
    .swiper-slide {
      padding: calc(var(--grid-gutter-x) / 2)
    }
    .swiper-pagination {
      margin-top: 30px;
      justify-content: center;
      --color: var(--color-dark-pagination);
      --color-hover: var(--color-dark-pagination-hover);
      --color-active: var(--color-dark-pagination-active);
    }
  }
  @include media-breakpoint-down(sm) {
    .swiper-container {
      overflow: visible;
    }
  }
}

.banner {
  display: flex;
  align-items: center;
  padding: 35px 85px 35px 60px;
  border-radius: 4px;
  overflow: hidden;
  position: relative;
  color: #fff;
  font-size: 1.125rem;
  .image {
    background-color: #EF8200;
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
  }
  &__title {
    font-weight: 700;
    margin-bottom: 10px;
  }
  &__text {
    flex: 1 1 auto;
    position: relative;
    z-index: 2;
    padding-right: 40px;
  }
  &__button {
    flex: 0 0 auto;
    position: relative;
    z-index: 2;
  }
  @include media-breakpoint-down(lg) {
    padding: 30px 60px;
  }
  @include media-breakpoint-down(md) {
    display: block;
    padding: 30px 20px;
    font-size: 1rem;
    &__text {
      padding: 0;
      margin: 0 0 20px 0;
    }
  }
}

.info-table {
  width: 100%;
  tr {
    &:not(:last-child) {
      td {
        padding-bottom: 10px;
      }
    }
  }
  td {
    padding: 0;
    span {
      display: inline;
      background-color: var(--color-background-4);
      position: relative;
      z-index: 2;
    }
    &:first-child {
      vertical-align: top;
      padding-right: 8px;
      overflow: hidden;
      color: var(--color-muted);
      span {
        padding-right: 8px;
        position: relative;
        &::after {
          content: '';
          display: block;
          border-bottom: 1px dashed currentColor;
          position: absolute;
          left: 100%;
          bottom: 0.25em;
          width: 100vw;
        }
      }
    }
    &:last-child {
      vertical-align: bottom;
      padding-left: 8px;
    }
  }
}

.video-block {
  .icon {
    position: absolute;
    top: calc(50% - 34px);
    left: calc(50% - 34px);
    width: 68px;
    height: 68px;
    border-radius: 50%;
    background-color: #fff;
    color: var(--color-accent-2);
    transition: transform $trs1, color $trs1;
    will-change: transform;
    padding: 5px;
    z-index: 3;
    svg {
      display: block;
      width: 100%;
      height: 100%;
    }
  }
  &::after {
    content: '';
    display: block;
    top: 0;
    left: 0;
    background-color: var(--color-muted);
    opacity: 0.5;
    z-index: 2;
    padding-top: 55%;
    position: relative;
  }
  &[data-touch], &[data-hover] {
    .icon {
      transform: scale(1.15);
      color: var(--color-accent-2_2);
    }
  }
}

.about-section {
  &__video {
    padding-left: 40px;
    order: 2;
  }
  &__container {
    display: grid;
    grid-template-columns: 1fr 1fr;
    column-gap: var(--grid-gutter-x);
  }
  @include media-breakpoint-down(lg) {
    &__container {
      display: block;
    }
    &__video {
      padding-left: 0;
      margin-bottom: 30px;
    }
  }
}

.certificate-block {
  position: relative;
  &::after {
    content: '';
    display: block;
    padding-top: 59%;
  }
  &::before {
    content: '';
    display: block;
    position: absolute;
    width: 100%;
    height: 100%;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background-color: #fff;
    border-radius: 4px;
    box-shadow: var(--block-shadow);
    transition: width $trs1, height $trs1;
  }
  &[data-hover] {
    &::before {
      width: calc(100% + 10px);
      height: calc(100% + 10px);
    }
  }
  .image {
    border-radius: 4px;
    overflow: hidden;
    position: absolute;
    width: 100%;
    height: 100%;
  }
}

.certificates-section {
  &__container {
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    gap: var(--grid-gutter-x);
  }
  @include media-breakpoint-down(lg) {
    &__container {
      grid-template-columns: repeat(3, 1fr);
    }
  }
  @include media-breakpoint-down(sm) {
    &__container {
      grid-template-columns: repeat(2, 1fr);
    }
  }
}

.blog-grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: var(--grid-gutter-x);
  @include media-breakpoint-down(lg) {
    grid-template-columns: repeat(2, 1fr);
  }
  @include media-breakpoint-down(md) {
    grid-template-columns: repeat(1, 1fr);
  }
}

.news-grid {
  display: grid;
  grid-auto-flow: dense;
  grid-template-columns: repeat(4, 1fr);
  gap: var(--grid-gutter-x);
  @include media-breakpoint-down(lg) {
    grid-template-columns: repeat(2, 1fr);
  }
}

.news-element {
  color: #fff;
  position: relative;
  background-color: var(--color-muted);
  border-radius: 4px;
  overflow: hidden;
  padding: 35px 20px 40px 40px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: flex-start;
  min-height: 260px;
  &[data-touch], &[data-hover] {
    .image {
      &::after {
        opacity: 1;
      }
    }
    .news-element__button {
      opacity: 1;
    }
  }
  &_wide {
    grid-column: span 2;
  }
  .image {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    &::after {
      content: '';
      display: block;
      position: absolute;
      width: 100%;
      height: 100%;
      top: 0;
      left: 0;
      background-color: rgba(43, 41, 41, 0.5);
      transition: opacity .5s $out;
      opacity: 0;
    }
    img {
      object-position: center right;
    }
  }
  &__text {
    position: relative;
    z-index: 2;
    margin-bottom: 25px;
  }
  &__date {
    margin-bottom: 8px;
    display: block;
  }
  &__title {
    font-size: 1rem;
    font-weight: 700;
    margin: 0;
  }
  &__button {
    .desktop & {
      transition: opacity .5s $out;
      opacity: 0;
    }
    .mobile & {
      .button {
        font-size: 0;
        padding: 0;
        position: absolute;
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        z-index: 2;
        &::before, &::after {
          display: none;
        }
      }
    }
    width: 100%;
  }
  @include media-breakpoint-down(xl) {
    padding: 30px 20px;
  }
  @include media-breakpoint-down(lg) {
    padding: 30px;
  }
  @include media-breakpoint-down(md) {
    min-height: 220px;
  }
  @include media-breakpoint-down(sm) {
    min-height: 180px;
    padding: 20px var(--container-gutter-x);
    &__title {
      font-size: 0.75rem;
    }
    &__text {
      margin-bottom: 20px;
    }
    &__date {
      font-size: 0.875rem;
    }
    &_wide {
      .news-element__title {
        font-size: 1rem;
      }
    }
  }
}

.catalogue {
  &__aside {
    grid-column: 1/2;
  }
  &__wrapper {
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    gap: var(--grid-gutter-x);
  }
  &__content {
    grid-column: 2/6;
  }
  &__items {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: var(--grid-gutter-x);
    &:not(:last-child) {
      margin-bottom: var(--grid-gutter-x);
    }
  }
  &__head {
    display: grid;
    align-items: center;
    grid-template-columns: repeat(4, 1fr);
    gap: var(--grid-gutter-x);
    margin-bottom: 40px;
  }
  &__status {
    grid-column: 2/5;
    display: flex;
    justify-content: flex-end;
    flex-wrap: wrap;
    gap: 10px var(--grid-gutter-x);
  }

  
  @include media-breakpoint-up(lg) {
    overflow: visible;
    &__wrapper {
      align-items: flex-start;
    }
    &__head-reset {
      display: none;
    }
  }
  @include media-breakpoint-down(xl) {
    &__wrapper {
      grid-template-columns: repeat(4, 1fr);
    }
    &__items, &__head {
      grid-template-columns: repeat(3, 1fr);
    }
    &__content {
      grid-column: 2/5;
    }
  }
  @include media-breakpoint-down(lg) {
    &__wrapper {
      display: block;
    }
    &__status {
      grid-column: 1/4;
      justify-content: flex-start;
      margin-top: 10px;
    }
  }
  @include media-breakpoint-down(md) {
    &__items, &__head {
      grid-template-columns: repeat(2, 1fr);
    }
    &__status {
      grid-column: 1/3;
    }
  }
  @include media-breakpoint-down(sm) {
    &__head {
      grid-template-columns: 1fr;
    }
    &__status {
      grid-column: 1/2;
    }
  }
}

.catalogue-filter {
  --gx: 12px;
  --gy: 12px;
  &__box {
    &:not(:last-child) {
      margin-bottom: 40px;
    }
  }
  &__category-head {
    display: block;
    padding: var(--gy) var(--gx);
    padding-right: 30px;
    position: relative;
    font-size: 16px;
    line-height: 20px;
    user-select: none;
    transition: color $trs1;
    .icon {
      --size: 14px;
      position: absolute;
      width: var(--size);
      height: var(--size);
      right: var(--gx);
      top: 50%;
      transform: translateY(-50%) rotate(-90deg);
      color: var(--color-accent-1);
    }
    &[data-touch], &[data-hover] {
      color: var(--color-accent-1);
    }
    &.is-active {
      font-weight: 700;
      color: var(--color-accent-1);
      .icon {
        transform: translateY(-50%) rotate(0);
      }
      &:not([data-toggle]) {
        cursor: default;
      }
    }
  }
  &__category {
    &:not(:last-child) {
      border-bottom: 1px solid var(--color-borders);
    }
  }
  &__category-content {
    &[data-toggle="content"] {
      display: none;
      &.is-active {
        display: block;
      }
    }
  }
  &__category-element {
    padding: var(--gy) var(--gx);
    &:not(:last-child) {
      border-bottom: 1px solid var(--color-borders);
    }
    .checkbox {
      &__label {
        &[data-touch], &[data-hover] {
          color: var(--color-accent-1);
        }
      }
    }
    a {
      display: block;
      &[data-touch], &[data-hover] {
        color: var(--color-accent-1);
      }
      &.is-active {
        color: var(--color-accent-1);
      }
    }
  }
  @include media-breakpoint-up(lg) {
    &-open {
      display: none;
    }
    &__head {
      display: none;
    }
    &__close-button {
      display: none !important;
    }
    &__container {
      .container {
        padding: 0;
      }
    }
  }
  @include media-breakpoint-down(lg) {
    display: flex;
    flex-direction: column;
    position: fixed;
    width: 100%;
    height: 100%;
    z-index: 1000;
    background-color: #fff;
    top: 0;
    left: 0;
    opacity: 0;
    visibility: hidden;
    pointer-events: none;
    transition: opacity $trs2, visibility $trs2;
    &.is-active {
      opacity: 1;
      visibility: visible;
      pointer-events: all;
    }
    &-close {
      margin: 0 0 0 auto;
    }
    &__head {
      flex: 0 0 auto;
      .container {
        height: 50px;
        display: flex;
        align-items: center;
      }
      box-shadow: 0px 5px 6px rgba(0, 0, 0, 0.05);
    }
    &__close-button {
      flex-shrink: 0;
      border-radius: 0;
      line-height: 60px;
      font-size: 16px;
    }
    &__container {
      flex: 1 1 auto;
      overflow: auto;
      padding: 50px 0;
    }
  }
  @include media-breakpoint-up(sm) {
    &__box {
      border-radius: 4px;
      box-shadow: var(--block-shadow);
      background: #fff;
    }
  }
  @include media-breakpoint-down(sm) {
    --gx: var(--grid-gutter-x);
    &__container {
      padding: 40px 0;
    }
    &__category {
      margin: 0 calc(var(--grid-gutter-x) * -1);
    }
  }
}

.article {
  &__container {
    border-radius: 4px;
    box-shadow: var(--block-shadow);
    background-color: #fff;
    padding: 0 40px 80px 40px;
  }
  &__head {
    padding: 80px 60px;
    overflow: hidden;
    border-radius: 0 0 4px 4px;
    position: relative;
    color: #fff;
    min-height: 350px;
    display: flex;
    margin-bottom: 55px;
  }
  &__image {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    &_mask {
      &::after {
        content: '';
        display: block;
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        position: absolute;
        z-index: 2;
        background: linear-gradient(90deg, rgba(44, 41, 41, 0.7) 50.09%, rgba(196, 196, 196, 0) 100%);
      }
    }
  }
  &__head-text {
    position: relative;
    z-index: 2;
    max-width: 650px;
    display: flex;
    flex-direction: column;
    justify-content: center;
  }
  &__date {
    font-size: 1.25rem;
  }
  @include media-breakpoint-up(md) {
    &__content {
      &::after {
        content: '';
        display: block;
        clear: both;
      }
      .video-block {
        float: right;
        width: 50%;
        margin: 0 0 0 20px;
      }
    }
  }
  @include media-breakpoint-down(lg) {
    &__head {
      padding: 60px 40px;
      min-height: 300px;
    }
    &__container {
      padding: 0;
    }
    &__content {
      padding: 0 40px 40px 40px;
    }
    &__date {
      font-size: 1.125rem;
    }
  }
  @include media-breakpoint-down(md) {
    &__content {
      .video-block {
        &:not(:last-child) {
          margin-bottom: 1.75rem;
        }
      }
    }
  }
  @include media-breakpoint-down(sm) {
    &__head {
      padding: 30px 20px;
      min-height: 200px;
      margin-bottom: 30px;
    }
    &__content {
      padding: 0 20px 30px 20px;
    }
    &__date {
      font-size: 1rem;
    }
  }
}

.image-slider {
