      margin-bottom: 50px;
    }
  }
  &__info-content {
    display: grid;
    align-items: flex-start;
    grid-template-columns: 1fr 1fr;
    column-gap: var(--grid-gutter-x);
    row-gap: 20px;
  }
  @include media-breakpoint-down(md) {
    &__info-content {
      display: block;
      .info-table {
        &:not(:last-child) {
          margin-bottom: 10px;
        }
      }
    }
  }
}

.large-images {
  display: grid;
  grid-template-columns: 1fr 1fr;
  column-gap: 100px;
  row-gap: 60px;
  &__block-title {
    color: var(--color-accent-1);
    margin: 0 0 40px 0;
  }
  &__block {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    .image {
      &::before {
        content: '';
        display: block;
        padding-top: 60%;
      }
    }
  }
  @include media-breakpoint-down(xl) {
    column-gap: 80px;
  }
  @include media-breakpoint-down(lg) {
    column-gap: 60px;
  }
  @include media-breakpoint-down(md) {
    column-gap: 40px;
  }
  @include media-breakpoint-down(sm) {
    grid-template-columns: 1fr;
    column-gap: var(--grid-gutter-x);
    row-gap: 40px;
  }
}

.map-section {
  --px: 26px;
  &:last-child {
    padding: 0;
  }
  &__container {
    position: relative;
  }
  &__content-wrapper {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    position: relative;
    z-index: 2;
    pointer-events: none;
    padding-bottom: 50px;
  }
  &__content {
    grid-column: span 6;
    pointer-events: all;
    background-color: #fff;
    padding: 0 var(--px);
  }
  &__map {
    display: block;
    position: absolute;
    height: 100%;
    width: 100%;
    top: 0;
    left: 0;
    border: 0;
    iframe {
      border: 0 !important;
      position: absolute;
      width: 100%;
      height: 100%;
      top: 0;
      left: 0;
    }
  }
  &__title {
    font-weight: 700;
    flex: 0 0 auto;
  }
  &__elements {
    margin: 0 calc(var(--px) * -1);
    overflow: auto;
    height: 440px;
    &::-webkit-scrollbar {
      width: 8px
    }
    &::-webkit-scrollbar-track {
      background-color: var(--color-scrollbar-track);
    }
    &::-webkit-scrollbar-thumb {
      background-color: var(--color-scrollbar-thumb);
    }
  }
  &__element {
    padding: 20px var(--px);
    display: flex;
    align-items: flex-start;
    &:not(:last-child) {
      border-bottom: 1px solid #F2F2F2;
    }
  }
  &__element-content {
    flex: 0 1 auto;
  }
  &__element-image {
    flex: 0 0 140px;
    margin-right: 25px;
    &::before {
      content: '';
      display: block;
      padding-top: 46%;
    }
    .icon {
      position: absolute;
      z-index: 2;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: 40px;
      height: 40px;
      border-radius: 50%;
      display: flex;
      background-color: #fff;
      color: var(--color-accent-1);
      border: 2px solid #fff;
      transition: color $trs1;
      svg {
        display: block;
        width: 100%;
        height: 100%;
      }
    }
    &[data-touch], &[data-hover] {
      .icon {
        color: var(--color-accent-1_2);
      }
    }
  }
  &__element-type {
    font-size: 0.875rem;
    margin-bottom: 10px;
  }
  &__element-title {
    font-weight: 700;
    color: var(--color-accent-1);
    margin-bottom: 2px;
    transition: color $trs1;
    &[data-touch], &[data-hover] {
      color: var(--color-accent-1_2);
    }
  }
  &__section-title {
    padding-left: var(--px);
  }
  @include media-breakpoint-down(lg) {
    &__element-image {
      flex: 0 0 110px;
      margin-right: 20px;
      &::before {
        content: '';
        display: block;
        padding-top: 60%;
      }
    }
  }
  @include media-breakpoint-down(md) {
    --px: calc(var(--content-gutter));
    &__map {
      position: relative;
      height: 400px;
      width: 100%;
    }
    &__content {
      grid-column: span 12;
    }
    &__content-wrapper {
      padding: 0;
      max-width: 100%;
    }
    &__section-title {
      padding-left: 0;
    }
    &__elements {
      height: auto;
    }
  }
  @include media-breakpoint-down(sm) {
    --px: var(--container-gutter-x);
    &__element-image {
      margin-right: 15px;
      flex: 0 0 100px;
    }
  }
}

.recipe-info {
  display: flex;
  &__item {
    display: flex;
    line-height: 20px;
    &:not(:last-child) {
      margin-right: 25px;
    }
    .icon {
      height: 20px;
      width: auto;
      margin-right: 10px;
      color: var(--color-accent-2);
    }
  }
}

.recipe-block {
  display: flex;
  flex-direction: column;
  position: relative;
  height: 100%;
  &__mask {
    content: '';
    display: block;
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    z-index: 3;
    mix-blend-mode: screen;
    pointer-events: none;
    &::before {
      content: '';
      background-color: #fff;
      display: block;
      position: absolute;
      width: calc(100% + 12px);
      height: calc(100% + 12px);
      top: -6px;
      left: -6px;
    }
    &::after {
      content: '';
      display: block;
      position: absolute;
      width: 100%;
      height: 100%;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      border-radius: 4px;
      background-color: #000;
      transition: width $trs1, height $trs1;
    }
  }
  &::before {
    content: '';
    background-color: #fff;
    display: block;
    position: absolute;
    width: calc(100% + 12px);
    height: calc(100% + 12px);
    top: -6px;
    left: -6px;
    pointer-events: none;
  }
  &::after {
    content: '';
    display: block;
    position: absolute;
    width: 100%;
    height: 100%;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    border-radius: 4px;
    pointer-events: none;
    box-shadow: var(--block-shadow);
    transition: width $trs1, height $trs1;
    z-index: 4;
  }
  &[data-hover] {
    &::after {
      width: calc(100% + 10px);
      height: calc(100% + 10px);
    }
    .recipe-block__mask {
      &::after {
        width: calc(100% + 10px);
        height: calc(100% + 10px);
      }
    }
  }
  &__image {
    flex: 0 0 270px;
    position: relative;
    z-index: 2;
    .image {
      border-radius: 4px 4px 0 0;
      position: absolute;
      width: calc(100% + 10px);
      height: calc(100% + 5px);
      bottom: 0;
      left: -5px;
      pointer-events: none;
    }
  }
  &__text {
    flex: 1 1 auto;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding: 35px 40px 40px 40px;
    position: relative;
    z-index: 2;
  }
  &__title {
    width: 100%;
    &:not(:last-child) {
      margin-bottom: 20px;
    }
  }
  @include media-breakpoint-down(sm) {
    &__image {
      flex: 0 0 250px;
    }
    &__text {
      padding: 30px 20px;
    }
  }
}

.recipe-day {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  position: relative;
  padding: 40px;
  height: 100%;
  &::before {
    content: '';
    display: block;
    position: absolute;
    width: 100%;
    height: 100%;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background-color: #fff;
    border-radius: 4px;
    border: 2px solid var(--color-accent-1);
    transition: width $trs1, height $trs1;
  }
  &[data-hover] {
    &::before {
      width: calc(100% + 10px);
      height: calc(100% + 10px);
    }
  }
  &__text-content {
    position: relative;
    z-index: 2;
  }
  &__bottom {
    position: relative;
    z-index: 2;
  }
  &__title {
    font-weight: 700;
    margin-bottom: 22px;
  }
  &__text-content {
    margin-bottom: 20px;
  }
  &__info {
    margin-bottom: 28px;
  }
  &__images {
    display: grid;
    grid-template-columns: 6fr 4fr;
    column-gap: var(--grid-gutter-x);
  }
  &__dish {
    border-radius: 4px;
    &::before {
      content: '';
      display: block;
      padding-top: 120%;
    }
  }
  @include media-breakpoint-down(sm) {
    padding: 30px 20px;
    &__dish {
      &::before {
        padding-top: 100%;
      }
    }
  }
}

.news-section {
  &__tag-list {
    margin-bottom: 60px;
  }
  @include media-breakpoint-down(sm) {
    &__tag-list {
      margin-bottom: 40px;
    }
  }
}

.recipe-description {
  display: grid;
  align-items: flex-start;
  grid-template-columns: repeat(10, 1fr);
  column-gap: var(--grid-gutter-x);
  row-gap: 40px;
  padding-bottom: 50px;
  margin-bottom: 50px;
  position: relative;
  border-bottom: 1px solid var(--color-borders);
  &__product {
    min-height: initial;
    max-width: 200px;
    grid-column: span 2;
  }
  @include media-breakpoint-up(xl) {
    &__section {
      grid-column: span 5;
      &:last-child {
        padding-left: 30px;
      }
    }
  }
  @include media-breakpoint-down(xl) {
    grid-template-columns: repeat(8, 1fr);
    &__section {
      grid-column: span 4;
    }
  }
  @include media-breakpoint-down(lg) {
    display: block;
    &__product {
      margin-top: 40px;
    }
  }
}

.production-section {
  &__bottom {
    display: grid;
    grid-template-columns: 1fr 1fr;
    column-gap: var(--grid-gutter-x);
  }
  &__top-text {
    margin-bottom: 70px;
  }
  &__advantages {
    margin-bottom: 70px;
  }
  @include media-breakpoint-down(lg) {
    &__bottom {
      grid-template-columns: 1fr;
      row-gap: 30px;
    }
    &__bottom-text {
      order: 2;
    }
    &__top-text {
      margin-bottom: 50px;
    }
    &__advantages {
      margin-bottom: 50px;
    }
  }
}

.production-advantages {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  column-gap: var(--grid-gutter-x);
  row-gap: 40px;
  &__item {
    display: flex;
    align-items: center;
    position: relative;
    padding: 0 20px;
    .icon {
      flex: 0 0 auto;
      width: 70px;
      margin-right: 20px;
    }
  }
  &__item-title {
    color: var(--color-accent-1);
    margin: 0;
  }
  @include media-breakpoint-up(lg) {
    &__item {
      &:not(:last-child) {
        &::after {
          content: '';
          display: block;
          position: absolute;
          width: 1px;
          height: 80%;
          top: 10%;
          right: calc(var(--grid-gutter-x) / -2);
          background-color: var(--color-borders);
        }
      }
    }
  }
  @include media-breakpoint-down(lg) {
    grid-template-columns: repeat(2, 1fr);
    &__item {
      padding: 0;
    }
  }
  @include media-breakpoint-down(md) {
    &__item {
      .icon {
        width: 50px;
      }
    }
  }
  @include media-breakpoint-down(sm) {
    grid-template-columns: 1fr;
  }
}

.shops-section {
  &__top {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    column-gap: var(--grid-gutter-x);
    margin-bottom: 60px;
  }
  &__item {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    column-gap: var(--grid-gutter-x);
    align-items: center;
    &:not(:last-child) {
      margin-bottom: 60px;
    }
  }
  &__item-image {
    position: relative;
    grid-column: span 5;
    iframe {
      display: block;
      position: absolute;
      width: 100%;
      height: 100%;
      top: 0;
      left: 0;
      border: none;
    }
  }
  @include media-breakpoint-up(lg) {
    &__item {
      &:nth-child(odd) {
        & > div {
          &:first-child {
            grid-column: 1/6;
          }
          &:last-child {
            grid-column: 8/13;
          }
        }
      }
      &:nth-child(even) {
        & > div {
          &:last-child {
            grid-column: 2/7;
          }
          &:first-child {
            grid-column: 8/13;
            order: 2;
          }
        }
      }
    }
  }
  @include media-breakpoint-between(md, lg) {
    &__item {
      &:nth-child(odd) {
        .shops-section__item-image {
          order: 2;
        }
      }
    }
    &__item-image, &__item-label {
      grid-column: span 6;
    }
  }
  @include media-breakpoint-down(lg) {
    &__top {
      grid-template-columns: 1fr;
      row-gap: 30px;
    }
    &__top-text {
      order: 2;
    }
  }
  @include media-breakpoint-down(md) {
    &__item {
      display: block;
    }
    &__item-image {
      margin-bottom: 30px;
    }
  }
  @include media-breakpoint-down(sm) {
    &__item {
      &:not(:last-child) {
        margin-bottom: 50px;
      }
    }
    &__top {
      margin-bottom: 50px;
    }
    &__item-image {
      margin-bottom: 20px;
    }
  }
}

.invitate-element {
  &__text {
    margin-bottom: 20px;
  }
  &__title {
    color: var(--color-accent-1);
    margin: 0;
  }
  @include media-breakpoint-down(md) {
    &__text {
      margin-bottom: 15px;
    }
  }
  @include media-breakpoint-down(sm) {
    &__text {
      margin-bottom: 10px;
    }
  }
}

.image-360 {
  display: block;
  position: relative;
  &::before {
    content: '';
    display: block;
    padding-top: 70%;
  }
  .icon {
    position: absolute;
    top: calc(50% - 34px);
    left: calc(50% - 34px);
    z-index: 2;
    width: 68px;
    height: 68px;
    padding: 15px;
    border-radius: 50%;
    background-color: #fff;
    color: var(--color-accent-2);
    transition: transform $trs1;
    will-change: transform;
    svg {
      display: block;
      width: 100%;
      height: 100%;
    }
  }
  &[data-touch], &[data-hover] {
    .icon {
      transform: scale(1.15);
      color: var(--color-accent-2_2);
    }
  }
}

.partners-box {
  padding: 40px;
  box-shadow: var(--block-shadow);
  &__container {
    overflow: hidden;
    border-radius: 4px;
    background-color: #fff;
    display: grid;
    grid-template-columns: repeat(8, 1fr);
    gap: 1px;
  }
  &__item {
    position: relative;
    &::after, &::before {
      content: '';
      display: block;
      position: absolute;
      background-color: #E1E1E1;
    }
    &::after {
      width: 1px;
      height: calc(300% + 2px);
      left: 100%;
      top: calc(-100% - 1px);
    }
    &::before {
      width: calc(300% + 2px);
      height: 1px;
      top: 100%;
      left: calc(-100% - 1px);
    }
    .image {
      &::before {
        content: '';
        display: block;
        padding-top: 71.4%;
      }
    }
  }
  @include media-breakpoint-down(lg) {
    &__container {
      grid-template-columns: repeat(4, 1fr);
    }
  }
  @include media-breakpoint-down(sm) {
    padding: 20px;
    &__container {
      grid-template-columns: repeat(2, 1fr);
    }
  }
}